import React, { Component } from 'react';
import moment from 'moment';
import DateSelector from './DateSelector';
import TogglSlider from './TogglSlider';
import Header from './Header';

class TogglSliderPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      moment: new moment(),
      today: new moment(new Date())
    };
  }

  handleDateChange = (moment) => {
    this.setState({ moment });
  }
  onPrevClick = (event) => {
    this.setState({ moment: this.state.moment.add(-1, 'd') });
  }
  onNextClick = (event) => {
    this.setState({ moment: this.state.moment.add(1, 'd') });
  }

  render() {
    const { togglSvcs, onLogout } = this.props;
    const { today, moment: momentProp } = this.state;

    return (
      <div className="TogglSliderPage">
        <Header isLoggedIn={togglSvcs !== null} onLogout={onLogout} />

        <div className="toggl-slider-page-content">
          <DateSelector
            selected={momentProp}
            nextDisabled={momentProp.isSame(today, "day")}
            maxDate={today}
            onChange={this.handleDateChange}
            onPrevClick={this.onPrevClick}
            onNextClick={this.onNextClick}
          />
          <TogglSlider
            togglSvcs={togglSvcs}
            date={momentProp.toDate()}
          />
        </div>
      </div>
    );
  }

}

export default TogglSliderPage;



