import React, { Component } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

class DateSelector extends Component {
  onPrevClick = (event) => {
    if (this.props.onPrevClick)
      this.props.onPrevClick(event);
  }

  onNextClick = (event) => {
    if (this.props.onNextClick)
      this.props.onNextClick(event);
  }

  onChange = (event) => {
    if (this.props.onChange)
      this.props.onChange(event);
  }

  render() {
    const { selected, maxDate, nextDisabled } = this.props;
    return (
      <div className="DateSelector">
        <button className="ui primary button prev-button" onClick={this.onPrevClick}>&lt;</button>
        <DatePicker className="date-picker"
          selected={selected}
          maxDate={maxDate}
          onChange={this.onChange}
        />
        <button className="ui primary button next-button" onClick={this.onNextClick} disabled={nextDisabled}>&gt;</button>
      </div>
    );
  }
}

export default DateSelector;



