import React from 'react';

class Header extends React.Component {

  onLogout = () => {
    if (this.props.onLogout) {
      this.props.onLogout();
    }
  }

  render() {
    const { isLoggedIn } = this.props;

    return (
      <div className="Header">
        <div className="title">
          <h1>toggl slider</h1>
          <p>
            See and adjust your toggl time entries with a slider<br />
            including synchronized adjustments of both entries where one begins right after the other stops
          </p>
        </div>

        {!isLoggedIn ||
          <div className="link-container">
            <button className="ui button basic link" onClick={this.onLogout}>Log Out</button>
          </div>
        }

      </div>

    );
  }



}

export default Header;



