import React, { Component } from 'react';
import './App.css';
import TogglSliderPage from './TogglSliderPage';
import LoginPage from './LoginPage';
import togglServices from './togglServices';

class App extends Component {
  togglApiTokenCookie = "toggl_api_cookie";

  constructor(props) {
    super(props);
    this.state = {
      togglApiToken: this.getCookie(this.togglApiTokenCookie),
      togglSvcs: null
    };
  }

  componentWillMount = () => {
    if (this.state.togglApiToken !== null) {
      this.doLogin(this.state.togglApiToken);
    }
  }

  // getCookie(cname) {
  //   var name = cname + "=";
  //   var decodedCookie = decodeURIComponent(document.cookie);
  //   var ca = decodedCookie.split(';');
  //   for (var i = 0; i < ca.length; i++) {
  //     var c = ca[i];
  //     while (c.charAt(0) === ' ') {
  //       c = c.substring(1);
  //     }
  //     if (c.indexOf(name) === 0) {
  //       return c.substring(name.length, c.length);
  //     }
  //   }
  //   return null;
  // }

  getCookie(name) {
    var v = document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
    return v ? v[2] : null;
  }

  setCookie(name, value, days) {
    var d = new Date();
    d.setTime(d.getTime() + 24 * 60 * 60 * 1000 * days);
    document.cookie = name + "=" + value + ";path=/;expires=" + d.toGMTString();
  }

  deleteCookie(name) { 
    this.setCookie(name, '', -1);
  }

  onLogin = (togglApiToken) => {
    return this.doLogin(togglApiToken);
  }

  doLogin = async (togglApiToken) => {
    const togglSvcs = new togglServices(togglApiToken);

    try {
      const userInfo = await togglSvcs.login();
      this.setState({ togglApiToken, togglSvcs, userInfo });
      this.setCookie(this.togglApiTokenCookie, togglApiToken);
    } catch (err) {
      alert("Login attempt failed");
    }
  }

  onLogout = () => {
    this.setState({ togglApiToken: null, togglSvcs: null, userInfo: null });
    this.deleteCookie(this.togglApiTokenCookie);
  }

  render() {
    if (this.state.togglApiToken) {
      return (
        <div className="App">
          <TogglSliderPage togglSvcs={this.state.togglSvcs} onLogout={this.onLogout} />
        </div>
      );
    }
    // else
    return (
      <div className="App">
        <LoginPage onLogin={this.onLogin} togglSvcs={this.state.togglSvcs} />
      </div>
    );
  }
}

export default App;



