// import TogglClient from 'toggl-api';
// import axios from 'axios';

class togglServices {

  constructor(togglApiToken) {
    this.baseUrl = "https://www.toggl.com/api/v8";
    this.togglApiToken = togglApiToken;
    this.togglApiTokenEncoded = btoa(`${togglApiToken}:api_token`); // btoa = base64encode
    this.authorization = `Basic ${this.togglApiTokenEncoded}`;
    // this.toggl = new TogglClient({ apiToken: togglApiToken });
  }

  login = () => {
    return fetch(this.baseUrl + '/me', {
      method: 'GET',
      credentials: "same-origin",
      cache: "no-cache",
      headers: {
        accept: 'application/json',
        authorization: this.authorization
      }
    })
      .then(response => {
        if (response.ok) {
          return response.json()
        } else {
          return Promise.reject('something went wrong!')
        }
      })
      .catch(error => {
        console.log('error is: ', error);
        throw Error(error);
      });


    // return axios.get('https://www.toggl.com/api/v8/me', {
    //   headers: {
    //     authorization: this.authorization        
    //   }
    // })
    //   .then(function (response) {
    //     return response;
    //   })
    //   .catch(function (error) {
    //     throw Error(error);
    //   });





    // const promise = new Promise((resolve, reject) => {
    //   this.toggl.getUserData(/*with_related_data*/false, (err, userData) => {
    //     if (err) {
    //       console.log(`getUserData failed: ${err}`);
    //       reject(Error(err));
    //     }
    //     else {
    //       resolve(userData);
    //     }
    //   });
    // });

    // return promise;
  }

  getTimeEntries = (start, end) => {
    const startISO = encodeURIComponent(start.toISOString());
    const endISO = encodeURIComponent(end.toISOString());
    return fetch(`${this.baseUrl}/time_entries?start_date=${startISO}&end_date=${endISO}`, {
      method: 'GET',
      headers: {
        accept: 'application/json',
        authorization: this.authorization
      }
    })
      .then(response => {
        if (response.ok) {
          return response.json()
        } else {
          return Promise.reject('something went wrong!')
        }
      })
      .catch(error => {
        console.log(`getTimeEntries failed: ${error}`);
        throw Error(error);
      });





    // const promise = new Promise((resolve, reject) => {
    //   this.toggl.getTimeEntries(start, end, (err, timeEntries) => {
    //     if (err) {
    //       console.log(`getTimeEntries failed: ${err}`);
    //       reject(Error(err));
    //     }
    //     else {
    //       resolve(timeEntries);
    //     }
    //   });
    // });

    // return promise;
  }



  updateTimeEntry = (timeEntry) => {
    const opts = { time_entry: timeEntry };
    return fetch(`${this.baseUrl}/time_entries/${timeEntry.id}`, {
      method: 'POST',
      body: JSON.stringify(opts),
      headers: {
        accept: 'application/json',
        authorization: this.authorization
      }
    })
      .then(response => {
        if (response.ok) {
          return response.json()
        } else {
          return Promise.reject('something went wrong!')
        }
      })
      .catch(error => {
        console.log(`updateTimeEntry failed: ${error}`);
        throw Error(error);
      });



    // const promise = new Promise((resolve, reject) => {
    //   this.toggl.updateTimeEntry(timeEntry.id, timeEntry, (err, timeEntry) => {
    //     if (err) {
    //       console.log(`updateTimeEntry failed: ${err}`);
    //       reject(Error(err));
    //     }
    //     else {
    //       resolve(timeEntry);
    //     }
    //   });
    // });

    // return promise;
  }

}

export default togglServices;



