import React, { Component } from 'react';
import Header from './Header';

class LoginPage extends Component {

  constructor(props) {
    super(props);

    this.state = {
      togglApiTokenInputValue: null
    }
  }

  onLoginButtonClick = (event) => {
    if (this.props.onLogin) {
      this.props.onLogin(this.state.togglApiTokenInputValue);
    }
  }

  onInputChange = (event) => {
    this.setState({ togglApiTokenInputValue: event.target.value });
  }

  onInputKeyPress = (target) => {
    if (target.charCode === 13) {
      target.preventDefault();
      this.onLoginButtonClick();
    }
  }

  render() {
    const { togglSvcs, onLogout } = this.props;

    return (
      <div className="LoginPage">
        <Header isLoggedIn={togglSvcs !== null} onLogout={onLogout} />

        <div className="login-form">

          <div className="ui labeled input toggl-api-token">
            <div className="ui label">
              Toggl API Token
            </div>
            <input type="text" placeholder="found on your toggl Profile Settings page" onChange={this.onInputChange} onKeyPress={this.onInputKeyPress} />
          </div>

          <button className="ui button" disabled={!this.state.togglApiTokenInputValue} onClick={this.onLoginButtonClick}>Log In</button>
        </div>

      </div>
    );
  }

}

export default LoginPage;



